<template>
    <div>
        estou em opes {{nivel}}
        <template v-if="nivel == 3">
            <div>
                aqui aparece se for outro nivel
            </div>
        </template>
        <template v-else-if="nivel == 2">
            <div>
                <el-button type="text" @click="addFilhos = true">Adicionar Filho</el-button>
            </div>
            <el-dialog title="Adicionar Filho" v-model="addFilhos">
                <el-input placeholder="Nome do filho" v-model="form.name"></el-input>
                <el-select v-model="form.turma" placeholder="Turma">
                    <el-option v-for="turma in turmas" :label="turma.label" :value="turma.value"></el-option>
                </el-select>
                <el-select v-model="form.periodo" placeholder="Perodo">
                    <el-option v-for="p in periodos" :label="p.label" :value="p.value"></el-option>
                </el-select>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addFilhos = false">Cancelar</el-button>
                    <el-button type="primary" @click="saveFilho">Adicionar Filho</el-button>
                </span>

            </el-dialog>

        </template>
        
    </div>
</template>
<script>

    import axios from 'axios';
    export default {
        data() {
            return {
                nivel: window.sessionStorage.getItem('nivel'),
                addFilhos: false,
                form: {
                    name: '',
                    turma: '',
                    periodo: ''
                },
                periodos: [{
                    label: 'MANH',
                    value: 'manha'
                }, {
                    label: 'TARDE',
                    value: 'tarde'
                }, {
                    label: 'INTEGRAL',
                    value: 'integral'
                }]
            }
        },
        watch: {
            addFilhos(novo) {
                if (!novo) {
                    this.form.name = '';
                    this.form.turma = '';
                    this.form.periodo = '';
                } 
            }
        },
        computed: {
            turmas() {
                let t = [];
                t.push({label: 'INFANTIL 1', value: 'inf1'});
                t.push({label: 'INFANTIL 2', value: 'inf2'});
                for (let i = 1; i < 10; i++) {
                    t.push({label: i + ' ANO', value: i + 'ano'});
                }
                return t;
            }
        },
        methods: {
            saveFilho() {
                const filho = Object.assign({}, this.form);
                console.log(filho);
                // remover dps
                this.addFilhos = false;
                return;

                axios.post('', filho).then(response => {
                    console.log('response');
                    console.log(response);
                }).catch(error => {
                    console.log('error');
                    console.log(error);
                });
            }
        }
    }
</script>
<style scoped></style>


